import { Component, OnInit } from '@angular/core';
import { DadesService } from '../services/dades.service';
@Component({
  selector: 'app-astra-zeneca',
  templateUrl: './astra-zeneca.page.html',
  styleUrls: ['./astra-zeneca.page.scss'],
})
export class AstraZenecaPage implements OnInit {
  public vacunesA: Array<any>;
  public donesA: number = 0;
  public homesA: number = 0;
  public numtotalA: number = 0;
  public dosiHA: number = 0;
  public dosiDA = 0;
  constructor(private dades: DadesService) { }

  ngOnInit() {
    this.dades. dadesVacunes().subscribe(
      (data: Array<any>) => {
        
        //Recollir dades i guardarles a les propietats totals
        this.vacunesA = data;

        this.vacunesA.forEach(element => {  

          if(element.fabricant == "Oxford / AstraZeneca"){
            
            this.numtotalA = +this.numtotalA+ +1
            if(element.sexe == "Home"){
             
              this.homesA = +this.homesA +1
              this.dosiHA = +this.dosiHA + +element.dosi
              
            }else{

       
              this.donesA = +this.donesA +1
              this.dosiDA = +this.dosiDA + +element.dosi
              

            }
          }
      
        });
      }
    )
    }

}
